<style>
.symbols-table-widget_wrapper {
  width: 800px;
  justify-content: space-between;
  padding: 20px;
}
.symbols-table-widget__row {
  color: #000;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  border-top: 1px solid #e5ddf9;
  cursor: pointer;
}
.symbols-table-widget__row.active {
  background: #f4f3f8;
}
.symbols-table-widget__row:hover {
  background: #f4f3f8;
}
.table-widget__column {
  display: flex;
  padding: 5px;
  align-items: center;
  justify-content: center;
  width: 20%;
  height: 40px;
}
.column__name {
  font-size: 20px;
  line-height: 20px;
  font-weight: bold;
}

.column__ask {
  color: #fd3b5e;
  font-size: 18px;
  line-height: 20px;
  font-weight: 500;
}

.column__bid {
  color: #49b47a;
  font-size: 18px;
  line-height: 20px;
  font-weight: 500;
}
.trend-symbol {
  padding: 5px;
}
.up-trend {
  color: #49b47a;
}
.down-trend {
  color: #fd3b5e;
}
</style>

<script>
import { onMount } from "svelte";
import { getSymbols } from "./getSymbols";
import { mainStore, setSymbols, setActiveSymbol } from "../../store";
import {
  getPercentage,
  getTrendSymbol,
  numberWithDigits,
} from "../../services/cell.services";
import Trend from "./Trend.svelte";

export let options;
const { brand, env } = options;

let { symbolsData, symbolsLoading, symbolsError, activeSymbol, assetGroup } =
  {};
let activeGroupSymbolsList = [];

onMount(() => {
  getSymbols({ brand, env });
});

mainStore.subscribe(
  ({ activeSymbol: activeSymbolFromStore, symbols, activeAssetGroup }) => {
    symbolsData = symbols.visibleSymbols;
    symbolsError = symbols.error;
    assetGroup = activeAssetGroup;
    symbolsLoading = symbols.loading;
    activeSymbol = activeSymbolFromStore;
  }
);

$: if (assetGroup && assetGroup.type) {
  setSymbols(assetGroup);

  if (!activeSymbol.name && symbolsData.length) {
    setActiveSymbol(symbolsData[0]);
  }
}

const handleTableRowClick = (symbol) => {
  console.log(">>> symbol", symbol);
  setActiveSymbol(symbol);
};

$: {
  console.log(">>>----------------------- activeSymbol1", activeSymbol);
  console.log(">>>----------------------- symbolsData", symbolsData);
}
</script>

<div class="symbols-table-widget_wrapper">
  {#if symbolsLoading}
    Symbols are loading...
  {:else if symbolsError}
    Error: {symbolsError}
  {:else}
    {#each symbolsData as symbol}
      <div
        class="{`symbols-table-widget__row ${
          symbol.name === activeSymbol.name ? 'active' : ''
        }`}"
        on:click="{() => {
          handleTableRowClick(symbol);
        }}"
      >
        <div class="table-widget__column column__name">
          {#if getTrendSymbol(symbol.trend)}
            <span class="trend-symbol"
              ><img src="../../assets/svg/up.svg" alt="up trend" /></span
            >
          {:else}
            <span class="trend-symbol"
              ><img src="../../assets/svg/down.svg" alt="down trend" /></span
            >
          {/if}
          {symbol.name.toUpperCase()}
        </div>
        <div class="table-widget__column column__trend">
          <Trend data="{symbol.trend}" />
        </div>
        <div class="table-widget__column column__percent">
          <span
            class="{`${
              getTrendSymbol(symbol.trend) ? 'up-trend' : 'down-trend'
            }`}">{getPercentage(symbol.trend)}</span
          >
        </div>
        <div class="table-widget__column column__ask">
          {numberWithDigits(symbol.ask, symbol.digits)}
        </div>
        <div class="table-widget__column column__bid">
          {numberWithDigits(symbol.bid, symbol.digits)}
        </div>
      </div>
    {/each}
  {/if}
</div>
