<style>
.asset-groups-widget-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}
.asset-groups-item {
  display: flex;
  color: #140533;
  padding: 13px 24px;
  background: #f4f3f8;
  border-radius: 32px;
  flex-direction: row;
  align-items: flex-start;
  margin-right: 16px;
}
.asset-groups-item:hover {
  cursor: pointer;
  background: #e0dfe3;
}
.asset-groups-item.active {
  color: #f4f3f8;
  background: #140533;
}
</style>

<script>
import { onMount } from "svelte";
import { getAssetGroups } from "./getAssetGroups";
import { mainStore, setActiveAssetGroup } from "../../store";

export let options;

const { brand, env } = options;

let {
  activeGroup,
  assetGroupsList,
  assetGroupsDataLoading,
  assetGroupsDataError,
} = {};

mainStore.subscribe(({ assetGroups, activeAssetGroup }) => {
  activeGroup = activeAssetGroup;
  assetGroupsList = assetGroups.visibleGroups;
  assetGroupsDataError = assetGroups.error;
  assetGroupsDataLoading = assetGroups.loading;
});

onMount(() => {
  getAssetGroups({ brand, env });
});

$: console.log(">>>----------------------- activeGroup", activeGroup);
</script>

<div class="asset-groups-widget-wrapper">
  {#if assetGroupsDataLoading}
    Asset groups loading...
  {:else if assetGroupsDataError}
    Error: {assetGroupsDataError}
  {:else}
    {#each assetGroupsList as assetGroup}
      <button
        class="{`asset-groups-item ${
          activeGroup.type === assetGroup.type ? 'active' : ''
        }`}"
        on:click="{() => {
          setActiveAssetGroup(assetGroup);
        }}">{assetGroup.type}</button
      >
    {/each}
  {/if}
</div>
