<style>
text {
  fill: #ffffff;
  dominant-baseline: center;
  font-size: 10px;
  text-anchor: middle;
}
</style>

<script>
import { getTrendSymbol } from "../../services/cell.services";

export let data = [];
const svgHeight = 40;
const svgWidth = 72;
let rectHeight = [];
$: min = Math.min(...data);
$: max = Math.max(...data);
$: yheight = max - min;
$: xwidth = data.length ? svgWidth / data.length : svgWidth;

$: if (data) {
  rectHeight = data.map((height) => {
    const res = Math.ceil((svgHeight * (max - height)) / yheight);
    return res > 0 ? res : 1;
  });
}
$: console.log(">>> rectHeight", rectHeight);
$: console.log(">>> data", data);
$: console.log(">>> min", min);
$: console.log(">>> max", max);
</script>

<!--<svg height="{svgHeight}" width="{svgWidth}" viewBox="15 0 10 72">-->
<svg
  height="{svgHeight}"
  width="{svgWidth}"
  fill="{`${getTrendSymbol(data) ? '#49b47a' : '#fd3b5e'}`}"
>
  {#each rectHeight as value, i}
    <rect
      y="{svgHeight - value}"
      width="{xwidth}"
      x="{i * xwidth}"
      height="{value}"></rect>
  {:else}
    <text x="20" y="20">No data provided</text>
  {/each}
  <!--  <polyline points="{rectHeight}"></polyline>-->
</svg>
